<template>

  <div>
    <div v-if="dataItem.title" class="title title-page">
      <div class="title-left">
        <h2>{{ dataItem.title }}</h2>
      </div>
    </div>

    <div
      v-for="(items, indexs) in dataItem.data"
      :key="indexs"
      class="common-main box"
    >
      <div
        v-for="(dxItems, dxIndexs) in items"
        :key="dxIndexs"
        class="page-list"
      >
        <div v-if="dxItems.type!==0" class="pagelist-left">{{ dxItems.name }}</div>
        <div v-if="dxItems.type === 1" class="pagelist-right">{{ dxIndexs!==3?dxItems.value:(dxItems.value===''?'未确认':(dxItems.value==='0'?'否':'是')) }}</div>
        <div v-else-if="dxItems.type === 9 || dxItems.type === 10 || dxItems.type === 11 || dxItems.type === 12 " class="pagelist-right  ">
          <div v-if="dxItems.files !== ''&& dxItems.files !== null ">
            <ul
              v-for="(fjItem, fjIndex) in dxItems.values"
              :key="fjIndex"
            >
              <li @click="loadFiles(fjItem,dxItems.files[indexs])">
                <i class="iconfont iconxiazai" />
                {{ fjItem }}
              </li>
            </ul>
          </div>
        </div>
      </div>
      <div class="pagelist-btn">
        <ul>
          <li><van-button type="info" size="small" plain="" round @click="show(indexs)">邀请函查看</van-button></li>
        </ul>
      </div>
      <van-popup v-model="status.isShowPublish" closeable :style="{width: '90%', height: '90%' }">
        <div class="popup-title">邀请函查看</div>
        <div class="popup-content" v-html="yqhxx" />
        <div v-if="value != '1' " class="popup-btn">
          <ul>
            <li @click="closeDialog()">取消</li>
            <li @click="yqhqrAction(0)">拒绝邀请</li>
            <li class="select" @click="yqhqrAction(1)">接受邀请</li>
          </ul>
        </div>
        <div v-else class="popup-btn">
          <ul>
            <li class="select" @click="closeDialog">完成</li>
          </ul>
        </div>
      </van-popup>
    </div>
    <div v-if="state" class="foot-btn noshadow">
      <ul>
        <li><van-button type="info" size="large" round @click="goBm()">报名</van-button></li>
      </ul>
    </div>
    <!-- 更多 end -->
    <div v-if="dataItem.totalpageno>1 && showMore" class="page-more" @click="goMore(dataItem)">
      查看更多信息
      <i class="iconfont iconarrowright" />
    </div>
  </div>

</template>

<script>

import { mapState } from 'vuex'
import { NativeJS } from '@/common/js/mixin.js'
export default {
  components: {

  },
  mixins: [NativeJS],
  /**
     * 表单 {id:"",title:"",type:"",data[]}
     * type: 1-单表 2-列表
     * 字段{name:"",value:"",type:"",files:[],values:[]}
     * type:1-text 9-file 10-pdf 11-img 12-html
     *
     *[{"id":null,"title":"推荐供应商","type":2,"data":[[
       {"name":"供应商名称","value":"9151010009971167XW","type":1,"file":null,"values":null,"files":null},
       ]
       ],"totalpageno":0,"showmore":"app/xm/getLxgys/50a7c716a3ec4ecebe7eae69d66f3091","sqdDataList":[]}]
     */
  props: {
    dataItem: { // 数据源
      type: null,
      required: true
    },
    showMore: { // 是否展示更多
      type: null,
      required: true
    }
  },
  data() {
    return {
      state: false,
      value: '',
      index: '',
      yqhxx: '',
      status: {
        isShowPublish: false,
        topNum: 30
      }
    }
  },
  computed: {
    ...mapState({
      userInfo: state => state.mh.mhUserInfo,
      wybmListItem: state => state.gyswybm.wybmListItem
      // gysyqk: state => state.wybm.yqhck
    })
  },
  watch: {

  },
  created() {
    const array = this.dataItem.data
    for (const items in array) {
      for (const item in array[items]) {
        if (array[items][item].value === '1') {
          this.state = true
        }
      }
    }
  },

  mounted() {
  },

  methods: {
    loadFiles(fjItem, url) {
      console.log('fjItem=====', fjItem)
      console.log('url=====', url)

      this.NativeDownFiles(url)
    },
    goBm() {
      this.$router.push({
        name: 'Xmfb'
      })
    },
    show(indexs) {
      this.index = indexs
      this.value = this.dataItem.data[indexs][3].value
      this.$store
        .dispatch('gyswybm/getGysbmXmYqhAction', { zzid: this.userInfo.zzid, ggid: this.dataItem.data[indexs][0].value })
        .then(res => {
          // this.$bus.$emit('changeFbxx', res) // bus触发事件
          this.yqhxx = res
          this.status.isShowPublish = true
        })
        .catch((error) => {
          // this.pageStyles = 3
          this.$toast(error)
          console.log(error)
          // this.loading = false
        })
    },
    goMore(item) {
      this.$emit('goMoreAction', item)
      const more = []
      more.push(item.showmore)
      more.push(item.title)
      this.$router.push({
        name: 'DbsxSjbqMore',
        params: { morexx: more }
      })
    },
    yqhqrAction(type) {
      this.$store.dispatch('gyswybm/getGysyqhqrAction', {
        zzid: this.userInfo.zzid,
        xmid: this.wybmListItem.xmid,
        ggid: this.dataItem.data[this.index][0].value,
        sftyyq: type
      }).then((res) => {
        this.res = res
        this.$store.commit('gyswybm/SET_YQHZT', type)
        this.status.isShowPublish = false
        if (type === 1) {
          this.state = true
          this.$bus.$emit('yqhChange')
        } else {
          this.$bus.$emit('yqhChange')
        }
      // eslint-disable-next-line no-unused-vars
      }).catch(error => {
        this.$toast.fail(error)
      })
    },
    yqhckAction() {
      this.status.isShowPublish = true
      // this.SET_GO_WYBM('true')
    },
    closeDialog() {
      if (this.yqhxx !== null && this.yqhxx !== 0) {
        this.status.isShowPublish = false
      } else {
        this.$toast.fail('暂无数据！')
      }
    }
  }
}
</script>

